
import { computed, defineComponent, reactive } from "vue";
import { fileStore } from "@rozbehsharahi/file-store";
import { Tag } from "@/model/tag";
export default defineComponent({
  props: {
    modelValue: {
      type: Array,
      required: false,
    },
  },
  setup(props, { emit }) {
    const services = reactive({
      database: fileStore.getDatabase(),
    });

    const methods = {
      isActive(tag: Tag) {
        return (props.modelValue || []).includes(tag.getIdentifier());
      },
      toggleTag(tag: Tag) {
        if (!props.modelValue) return;

        emit(
          "update:modelValue",
          methods.isActive(tag)
            ? props.modelValue.filter((v) => v !== tag.getIdentifier())
            : [...props.modelValue, tag.getIdentifier()]
        );
      },
    };

    return {
      services,
      methods,
      tags: computed(() => services.database.all("tag") as Tag[]),
    };
  },
});
